<template>
  <div class="addPopular">
    <h3>修改舆情</h3>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
    <el-form-item label="市场情绪" prop="ins_type">
        <el-radio-group v-model="ruleForm.active">
          <el-radio label="看空"></el-radio>
          <el-radio label="震荡"></el-radio>
          <el-radio label="看多"></el-radio>
        </el-radio-group>
      </el-form-item>
       <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      typeList: ["看空", "震荡", "看多"],
      ruleForm: {
        active: "",
      },
    };
  },
  methods: {
    format(value) {
      let date = new Date(value);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            active: this.typeList.indexOf(this.ruleForm.active),
            id: this.$route.params.id,
          };
          this.$x.post(this.url.newsupdate, params).then(res => {
            if(this.$route.params.active){
              var message = '修改成功'
            }else{
              var message = '添加成功'
            }
            if (res.status) {
              this.$message({
                message: message,
                type: "success"
              });
              this.$router.push("/popularList");
            }
          });
          console.log(params);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  mounted(){
    console.log(this.$route.params)
      this.ruleForm.active= this.$route.params.active
  }
};
</script>
<style  scoped>
.addPopular {
  border: 1px solid rgb(238, 238, 238);
  height: 100%;
  padding: 20px;
}
.addPopular h3 {
  margin-bottom: 20px;
  font-family: "Arial Negreta", "Arial Normal", "Arial";
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  height: 22px;
  line-height: 22px;
}
.form-group {
  margin-bottom: 10px;
}
.control-gtoup,
.control-label {
  float: left;
  line-height: 36px;
  margin-right: 20px;
}
.control-gtoup .el-input {
  width: 200px;
}

.control-label {
  width: 70px;
  text-align: right;
  margin-right: 10px;
}
.control-gtoup_w {
  width: calc(100% - 104px);
}
.form-group span {
  line-height: 38px;
  color: #666666;
}
.control-gtoup-table {
  width: calc(100% - 104px);
  padding: 20px;
}
.btn-box {
  text-align: center;
}
.el-button {
  margin-top: 30px;
  width: 123px;
}
</style>